@charset "UTF-8";
.flex-row {
  display: flex;
  flex-direction: row; }

.flex-col {
  display: flex;
  flex-direction: column; }

.j-c {
  justify-content: center; }

.a-c {
  align-items: center; }

.a-fs {
  align-items: flex-start; }

.a-b {
  align-items: baseline; }

.flex-1 {
  flex: 1 0 0; }

.flex-2 {
  flex: 2 0 0; }

.flex-2-5 {
  flex: 2.5 0 0; }

.j-sb, .facilities .content {
  justify-content: space-between; }

.j-fe {
  justify-content: flex-end; }

.facilities {
  height: 100%;
  background: url("./../../img/extras/flowers.jpg");
  background-size: cover;
  background-position: 0 50%;
  background-attachment: fixed;
  padding-top: 125px; }
  .facilities .content {
    padding-top: 30px; }
    .facilities .content ul {
      flex-basis: 40%;
      font-size: 24px; }
      .facilities .content ul li {
        padding: 10px 20px; }
        .facilities .content ul li:before {
          content: '→'; }
  .facilities .bottom {
    padding-top: 40px;
    font-size: 24px;
    text-align: center; }
    .facilities .bottom li {
      padding-bottom: 20px; }
      .facilities .bottom li:before {
        color: #29166f;
        content: '→'; }

@media only screen and (max-width: 767px) {
  .facilities {
    height: auto;
    padding-top: 100px;
    background-position: 20% 50%; }
    .facilities .content .content {
      flex-direction: column; }
    .facilities .bottom {
      text-align: left;
      padding-top: 0; }
      .facilities .bottom li:before {
        color: #29166f;
        content: '→'; } }
